import React, { useEffect, useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { login, removeError } from '../actions/auth';
import axios from 'axios';
import login_main_image from '../img/Icon/login.jpg'
import fb from '../img/Icon/fb-icon.png'
import google from '../img/Icon/google-icon.png'

const Login = ({ login,removeError, isAuthenticated ,isError}) => {
    const [formData, setFormData] = useState({
        email: '',
        password: '' 
    });
    const [error , setError] = useState(false)

    useEffect(()=>{
        if (isError) {
            setError(true)
            console.log('there is an error')
        }
        
    },[isError,error])

    const { email, password } = formData;

    const onChange = e => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
        setError(false)
        removeError()

    }

    const onSubmit =(e)  => {
        e.preventDefault();

        login(email, password)
    };

    const continueWithGoogle = async () => {
        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/o/google-oauth2/?redirect_uri=${process.env.REACT_APP_API_URL}/google`)

            window.location.replace(res.data.authorization_url);
        } catch (err) {

        }
    };

    const continueWithFacebook = async () => {
        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/o/facebook/?redirect_uri=${process.env.REACT_APP_API_URL}/facebook`)

            window.location.replace(res.data.authorization_url);
        } catch (err) {

        }
    };

    if (isAuthenticated) {
        return <Redirect to='/' />
    }
   

    return (

        <div className="container h-100">
        <div className="d-flex justify-content-center align-items-center h-100">
        <div className="login-panel">
          <div className="row">
            <div className="col-xs-12 col-sm-6">
              <div className="login-banner" style={{
                                background: `url(${login_main_image})`, 
                                backgroundRepeat: "no-repeat",
                                height: "100%",
                                borderRadius: "20px 0 0 20px",
                                background: "cover",
                                backgroundPosition: "right"
              }}>
                
              </div>
            </div>
            <div className="col-xs-12 col-sm-6">
             <div className="login-form">
                <div className="login-logo">
                  <img  src="img/logo.png" alt="Logo Here" />
                </div>
                <form onSubmit={e => onSubmit(e)}>
                  <div className="input-field">
                    <label>Email Address</label>
                    <input
                        className='form-controls'
                        style={error? {border:'2px solid red'}:null}

                        type='email'
                        placeholder='Email'
                        name='email'
                        value={email}
                        onChange={e => onChange(e)}
                        required
                    />
                  </div>
                  <div className="input-field">
                    <label>Password</label>
                    <input
                        style={error? {border:'2px solid red'}:null}
                        className='form-controls'
                        type='password'
                        placeholder='Password'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />  
                  </div>
                  <div className="checkbox custom-checkbox pull-left">
                    <label>
                      <input type="checkbox"/>
                      <span className="checkbox-decorator"><span className="check"></span>
                      </span> Remember me 
                    </label>
                  </div>
                  {error?
                    <div className="pull-left"> 
                           <p style={{fontSize:'10px',textAlign:'center',color:'red'}}> Your account is not found with the given credentials </p>
                     </div>

                  :
                  null
                  }
                  <div className="pull-right"><a href="/forgot-password"> Forgot Password? </a> </div>
                  <div className="form-group">
                    <input type='submit' value="Sign In" className="btn btn-info btn-lg btn-block login-btn ripple"/>
                  </div>
                  <div className="header-title">
                    <h2><span className="tile-login">Login With</span></h2>
                  </div>
                  <div className="social-login-btns">
                    <ul>
                      <li>
                        <a >
                        <img src={fb} alt="Facebook"onClick={continueWithFacebook} />
                        </a>
                      </li>
                      <li>
                        <a >
                        <img src={google} alt="Google" onClick={continueWithGoogle} />
                        </a>
                      </li>
                    </ul>
                  </div>
                </form> 
                {/* <button className='btn btn-danger mt-3' onClick={continueWithGoogle}>
                    Continue With Google
                </button>
                <br />
                <button className='btn btn-primary mt-3' onClick={continueWithFacebook}>
                    Continue With Facebook
                </button> */}
             </div>  
            </div>
          </div>  
        </div>
      </div>
    </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    isError:state.auth.isError
});

export default connect(mapStateToProps, { login,removeError })(Login);
